/**
 *  Define scss variables here.
 *
 *  Available options for Bootstrap:
 *  http://getbootstrap.com/customize/
 *
 */
.container {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
}

.container .background {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.container .content {
  display: inline-block;
  position: relative;
}

.container .content > div {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.container .content > div div {
  display: block;
  margin: 0 auto;
  width: 15%;
  height: 15%;
  border-radius: 100%;
  animation: loadingDelay 1.2s infinite ease-in-out both;
}

.container .content > div:nth-child(2) {
  transform: rotate(30deg);
}

.container .content > div:nth-child(2) div {
  animation-delay: -1.1s;
}

.container .content > div:nth-child(3) {
  transform: rotate(60deg);
}

.container .content > div:nth-child(3) div {
  animation-delay: -1s;
}

.container .content > div:nth-child(4) {
  transform: rotate(90deg);
}

.container .content > div:nth-child(4) div {
  animation-delay: -0.9s;
}

.container .content > div:nth-child(5) {
  transform: rotate(120deg);
}

.container .content > div:nth-child(5) div {
  animation-delay: -0.8s;
}

.container .content > div:nth-child(6) {
  transform: rotate(150deg);
}

.container .content > div:nth-child(6) div {
  animation-delay: -0.7s;
}

.container .content > div:nth-child(7) {
  transform: rotate(180deg);
}

.container .content > div:nth-child(7) div {
  animation-delay: -0.6s;
}

.container .content > div:nth-child(8) {
  transform: rotate(210deg);
}

.container .content > div:nth-child(8) div {
  animation-delay: -0.5s;
}

.container .content > div:nth-child(9) {
  transform: rotate(240deg);
}

.container .content > div:nth-child(9) div {
  animation-delay: -0.4s;
}

.container .content > div:nth-child(10) {
  transform: rotate(270deg);
}

.container .content > div:nth-child(10) div {
  animation-delay: -0.3s;
}

.container .content > div:nth-child(11) {
  transform: rotate(300deg);
}

.container .content > div:nth-child(11) div {
  animation-delay: -0.2s;
}

.container .content > div:nth-child(12) {
  transform: rotate(330deg);
}

.container .content > div:nth-child(12) div {
  animation-delay: -0.1s;
}

@-webkit-keyframes loadingDelay {
  0%, 39%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}

@keyframes loadingDelay {
  0%, 39%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}



