@import '../../../theme/variables.scss';

.job {
  position: relative;
  display: block;
  text-decoration: none !important;
  // overflow: hidden;

  img {
    float: left;
    width: 80px;
    height: 80px;
    border-radius: 40px;
    margin: 20px 0 20px 30px;        
  }

  .content {
    display: inline-flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: calc(100% - 160px);
    height: 80px;
    margin: 20px 30px 20px 20px;

    > div, .title {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }

    .info {
      display: flex;
      justify-content: space-between;

      .title {
        font-size: 16px;
        font-weight: 500;
      }
  
      .distance {
        color: $text-color2;
        margin-left: 15px;
      }
    }
  }

  .controls {
    position: absolute;
    display: none;//display: flex;
    flex-direction: column;
    justify-content: center;
    width: 150px;
    top: 0;
    bottom: 0;
    right: 0;//-150px;
    padding: 0 30px;
    background-color: rgba(0,0,0,0.2);
    // transition: right 0.3s ease;
    
    button {
      padding: 4px 12px;
      margin: 5px 0;
    }
  }
}

.job::before {
  content: '';
  display: block;
  border-top: 1px solid $border-color;
  margin: 0 30px;
}

.job:first-child::before {
  display: none;
}

a.job:hover {
  background-color: $list-item-hover-color;

  .controls {
    display: flex;// right: 0;    
  }
}

a.job:hover::before,
a.job:hover + .job::before {
  margin: 0;
}

div.job > div {
  position: relative;
  height: 120px;
}



