@import '../../../theme/variables.scss';

.root {
  min-height: 400px;

  .splashImage {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-size: cover;
    background-position: center;  
    transition: opacity 2000ms ease;
  }
  
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    text-align: center;
  
    .title {
      color: white;
      font-size: 2.3em;
      margin-bottom: 30px;
    }
  
    .register {
      display: inline-block;
      width: 250px;
      margin: 0 15px;
      margin-bottom: 25px;
      padding: 5px 0;    
      color: #fff;
      border: 1px solid white;
      border-radius: 250px;
      text-decoration: none;
      text-align: center;
      transition: background-color 0.5s ease 0s;

      span:first-child {
        font-size: 16px;
      }
      span:last-child {
        font-size: 24px;
      }
    }
  
    .register:first-child {
      background-color: rgba(0, 182, 164, 0.5);    
    }
    .register:first-child:hover {
      background-color: rgba(0, 182, 164, 1);
    }
  
    .register:last-child {
      background-color: rgba(255, 147, 0, 0.5);
    }
    .register:last-child:hover {
      background-color: rgba(255, 147, 0, 1);    
    }
  }
  
  .appButton > img {
    height: 50px;
    margin: 10px;
  }
  
}



