@import '../../../theme/variables.scss';

.root {
  text-align: center;
  padding: 15px 0;

  :global(.board) {
    display: inline-block;
    width: 350px;

    h3 {
      margin-top: 0;
      margin-bottom: 35px;
      text-transform: uppercase;
      text-align: left;
    }

    form {
      button {
        width: 100%;
        margin-top: 10px;   
      }
    }

    :global(.form-group:last-child) {
      margin-bottom: 0;
    }
  }
}


